import React from 'react';
import ShowList from '../containers/show-list';
// import ShowDetail from '../containers/show-detail';
import './app.css';

function Copyrights() {
  var fromYear = '2018';
  var toYear = '';
  var cYear = new Date().getFullYear();
  console.log(cYear);
  if (cYear > 2018) {
    toYear = ' - ' + cYear;
  }
  return 'Copyrights © ' + fromYear + toYear + ' vkcg | All Rights Reserved.';
}
function App() {
  return (

    <div>
      <div className='bg'></div>
      <div className='bg1'></div>
      <div className='bg2'></div>

      <div className='container-fluid'>
        <div style={{ backgroundColor: 'transparent' }} className='jumbotron jumbotron-fluid'>
          <div className='container'>
            <h1 className='display-4'>ThatShow</h1>
            <hr />
            <h3>Top Popular Tv Shows All Around The World</h3>
          </div>
        </div>
      </div>
      <ShowList />
      <hr />
      {/* <h2>Show Details:</h2>
            <ShowDetail/> */}
      <div className="bgcontact"></div>
      <div id="contact" align="center">
        <h3>Contact:</h3>
        <address>
          <p>EMail: varunkcg@outlook.com</p>
        </address>
        <p id="crights">{Copyrights()}</p>
        <hr />
        <p>Data provided by </p>
        <img style={{ width: '50px', height: 'auto' }} src="https://www.themoviedb.org/assets/1/v4/logos/208x226-stacked-blue-e6df1ff1a41c48555a15336ae8a6b3c6f77dfae41d2a50b78e4794c1ce048792.png" />
      </div>
    </div>
  )
}

export default App;


