import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { selectShow } from '../actions';
import ShowDetail from './show-detail';
import './showcard.css';
import { loadShows } from '../actions/load-show';
import Pagination from "react-js-pagination";
import Loader from '../Loader';
import '../Loader.css';

class ShowList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activePage: 1,
      pageCount: 1000,
      pageRange: 10
    };
    this.handlePageChange = this.handlePageChange.bind(this);
  }

  handlePageChange(pageNumber) {
    console.log(`Viewing Page: ${pageNumber}`);
    this.setState({ activePage: pageNumber });
    this.props.selectPage(pageNumber);
  }
  createListItems() {
    // if (this.state.pageCount === 0) {
    //   this.setState({
    //     // pageCount: this.props.shows['total_pages'] - 1
    //     // Max 1000
    //   });
    //   console.log(this.props.shows["total_pages"]);
    // }
    return this.props.shows['results'].map((show) => {
      return (
        <div className="col-md-3 text-center" key={show.id}>
          <a align='center' onClick={() => this.props.selectShow(show)} data-toggle="modal" data-target="#exampleModalCenter">
            <img className='showcardbox' src={('https://image.tmdb.org/t/p/w500' + show.poster_path)} alt={show.name} style={{ cursor: 'pointer', width: '200px', height: 'auto', margin: '20px 0px' }} />
            <br/>
            <p className="title" style={{ cursor: 'pointer' }}>{show.name}</p>
          </a>
          <div className="modal fade" id="exampleModalCenter" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalCenterTitle"></h5>
                  <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">
                  <ShowDetail />
                </div>
                <div className="modal-footer">
                  <button type="button" className="btn btn-danger" data-dismiss="modal">Close</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    });
  }
  render() {
    return (
      <div className='container'>
        <Pagination
          innerClass="pagination justify-content-center flex-wrap"
          itemClass="page-item"
          linkClass="page-link"
          activePage={this.state.activePage}
          itemsCountPerPage={1}
          totalItemsCount={this.state.pageCount}
          pageRangeDisplayed={this.state.pageRange}
          onChange={this.handlePageChange}
        />
        <div className='row'>
          {this.props.shows && this.createListItems()}
          <div className='col'>
            {!this.props.shows && <Loader />}
          </div>
        </div>
        <p style={{backgroundColor:'wheat',display:'inline-block',padding:'10px'}}>Shows : {this.state.activePage*20-19} - {this.state.activePage*20} || Page : {this.state.activePage} of {this.state.pageCount}</p>
        <Pagination
          innerClass="pagination justify-content-center flex-wrap"
          itemClass="page-item"
          linkClass="page-link"
          activePage={this.state.activePage}
          itemsCountPerPage={1}
          totalItemsCount={this.state.pageCount}
          pageRangeDisplayed={this.state.pageRange}
          onChange={this.handlePageChange}
        />
      </div>
    );
  }
}

function mapStateToProps(state) {
  // console.log(state.shows)
  return {
    shows: state.shows
  };
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators({ selectShow: selectShow, selectPage: loadShows }, dispatch)
}
export default connect(mapStateToProps, matchDispatchToProps)(ShowList);


